@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

.welcome-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 50%, #334155 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  position: relative;
  overflow: hidden;
}

.welcome-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 0;
}

.floating-food {
  position: absolute;
  font-size: 2rem;
  opacity: 0.1;
  animation: float 6s ease-in-out infinite;
}

.floating-food:nth-child(1) {
  top: 10%;
  left: 10%;
  animation-delay: 0s;
}

.floating-food:nth-child(2) {
  top: 20%;
  right: 15%;
  animation-delay: 1s;
}

.floating-food:nth-child(3) {
  bottom: 30%;
  left: 20%;
  animation-delay: 2s;
}

.floating-food:nth-child(4) {
  bottom: 20%;
  right: 10%;
  animation-delay: 3s;
}

.floating-food:nth-child(5) {
  top: 50%;
  left: 5%;
  animation-delay: 4s;
}

.floating-food:nth-child(6) {
  top: 70%;
  right: 5%;
  animation-delay: 5s;
}

.welcome-container {
  max-width: 1200px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  position: relative;
  z-index: 1;
}

.welcome-header {
  text-align: center;
  animation: slideInDown 0.8s ease-out;
}

.welcome-logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  margin-bottom: 24px;
}

.logo-icon {
  width: 80px;
  height: 80px;
  border-radius: 20px;
  background: linear-gradient(135deg, #3b82f6, #10b981);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  color: #fff;
  font-size: 2rem;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  animation: logoSpin 3s ease-in-out infinite;
}

.welcome-logo h1 {
  font-size: 3.5rem;
  font-weight: 700;
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  margin: 0;
}

.welcome-subtitle {
  font-size: 1.2rem;
  color: rgba(203, 213, 225, 0.8);
  margin: 0;
  max-width: 600px;
  line-height: 1.6;
}

.title-row {
  display: flex;
  align-items: center;
  gap: 12px;
  justify-content: center;
}

.dev-badge {
  background: rgba(255,255,255,0.06);
  color: #fff;
  padding: 6px 10px;
  border-radius: 999px;
  font-size: 0.85rem;
  font-weight: 600;
  box-shadow: inset 0 -2px 0 rgba(0,0,0,0.08);
}

.quick-features {
  margin-top: 12px;
  display: flex;
  gap: 8px;
  justify-content: center;
  flex-wrap: wrap;
}

.chip {
  background: rgba(255,255,255,0.03);
  padding: 6px 10px;
  border-radius: 999px;
  color: rgba(203,213,225,0.9);
  font-weight: 600;
  font-size: 0.9rem;
  border: 1px solid rgba(255,255,255,0.04);
  transition: transform 0.18s ease, background 0.18s;
}

.chip:hover {
  transform: translateY(-4px);
  background: rgba(59,130,246,0.12);
  color: #fff;
  border-color: rgba(59,130,246,0.18);
}

.password-field {
  position: relative;
  display: flex;
  align-items: center;
}

.show-pass-btn {
  position: absolute;
  right: 12px;
  background: none;
  border: none;
  color: rgba(203,213,225,0.9);
  cursor: pointer;
  font-size: 1.1rem;
  padding: 6px;
  border-radius: 8px;
  transition: transform 0.15s;
}

.show-pass-btn:hover { transform: translateY(-2px); }

.strength-meter { margin-top: 8px; display: flex; align-items: center; gap: 8px; }

.strength-bar { width: 80px; height: 8px; background: rgba(255,255,255,0.06); border-radius: 8px; overflow: hidden; position: relative; }
.strength-bar::after { content: ''; position: absolute; top: 0; left: 0; height: 100%; width: 0%; background: linear-gradient(90deg,#ef4444,#f97316,#10b981); transition: width 0.3s ease; }
.strength-bar.s1::after { width: 30%; background: linear-gradient(90deg,#ef4444,#f97316); }
.strength-bar.s2::after { width: 60%; background: linear-gradient(90deg,#f97316,#f59e0b,#10b981); }
.strength-bar.s3::after { width: 100%; background: linear-gradient(90deg,#f59e0b,#10b981); }

.strength-label { font-size: 0.85rem; color: rgba(203,213,225,0.8); }

.auth-guest-btn { margin-top: 8px; background: transparent; border: 1px solid rgba(255,255,255,0.06); color: rgba(255,255,255,0.9); padding: 12px 20px; border-radius: 12px; cursor: pointer; transition: all 0.18s ease; }
.auth-guest-btn:hover { transform: translateY(-2px); background: rgba(255,255,255,0.03); }




.auth-card {
  background: linear-gradient(145deg, rgba(255,255,255,0.1), rgba(255,255,255,0.05));
  border: 1px solid rgba(255,255,255,0.12);
  border-radius: 24px;
  padding: 40px;
  backdrop-filter: blur(20px);
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  width: 100%;
  max-width: 450px;
  animation: slideInUp 0.8s ease-out 0.2s both;
}

.auth-tabs {
  display: flex;
  background: rgba(255,255,255,0.05);
  border-radius: 16px;
  padding: 4px;
  margin-bottom: 32px;
}

.auth-tab {
  flex: 1;
  padding: 12px 24px;
  border: none;
  background: transparent;
  color: rgba(203, 213, 225, 0.7);
  border-radius: 12px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  font-size: 1rem;
}

.auth-tab.active {
  background: linear-gradient(135deg, #3b82f6, #2563eb);
  color: #fff;
  box-shadow: 0 4px 15px rgba(59, 130, 246, 0.3);
}

.auth-tab:hover:not(.active) {
  color: #fff;
  background: rgba(255,255,255,0.1);
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  color: #fff;
  font-weight: 600;
  font-size: 0.9rem;
}

.form-input {
  padding: 18px 24px;
  border: 2px solid rgba(255,255,255,0.15);
  border-radius: 16px;
  background: rgba(255,255,255,0.08);
  color: #fff;
  font-size: 1.1rem;
  width: 100%;
  box-sizing: border-box;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  min-height: 56px;
}

.form-input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 4px rgba(59, 130, 246, 0.15), 0 4px 12px rgba(59, 130, 246, 0.2);
  background: rgba(255,255,255,0.12);
  transform: translateY(-2px);
}

.form-input::placeholder {
  color: rgba(203, 213, 225, 0.5);
}

.form-input:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.error-message {
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  color: #fca5a5;
  padding: 12px 16px;
  border-radius: 12px;
  font-size: 0.9rem;
  text-align: center;
  animation: shake 0.5s ease-in-out;
}

.auth-submit-btn {
  background: linear-gradient(135deg, #3b82f6, #2563eb);
  border: none;
  color: #fff;
  padding: 16px 24px;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 600;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  box-shadow: 0 10px 25px rgba(59, 130, 246, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  margin-top: 8px;
}

.auth-submit-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 15px 35px rgba(59, 130, 246, 0.4);
}

.auth-submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

.spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255,255,255,0.3);
  border-top: 2px solid #fff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.auth-switch {
  text-align: center;
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid rgba(255,255,255,0.1);
}

.auth-switch p {
  color: rgba(203, 213, 225, 0.8);
  margin: 0;
}

.switch-btn {
  background: none;
  border: none;
  color: #3b82f6;
  cursor: pointer;
  font-weight: 600;
  text-decoration: underline;
  transition: color 0.3s ease;
}

.switch-btn:hover {
  color: #2563eb;
}

.welcome-features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 32px;
  width: 100%;
  max-width: 900px;
  animation: fadeInUp 0.8s ease-out 0.4s both;
}

.feature {
  text-align: center;
  padding: 32px 24px;
  background: linear-gradient(145deg, rgba(255,255,255,0.08), rgba(255,255,255,0.03));
  border: 1px solid rgba(255,255,255,0.1);
  border-radius: 20px;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.feature:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
  border-color: rgba(59, 130, 246, 0.3);
}

.feature-icon {
  font-size: 3rem;
  margin-bottom: 16px;
  animation: bounce 2s infinite;
}

.feature h3 {
  color: #fff;
  font-size: 1.3rem;
  font-weight: 600;
  margin: 0 0 12px 0;
}

.feature p {
  color: rgba(203, 213, 225, 0.8);
  margin: 0;
  line-height: 1.6;
}

/* Animations */
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

@keyframes logoSpin {
  0%, 100% {
    transform: rotate(0deg) scale(1);
  }
  50% {
    transform: rotate(5deg) scale(1.05);
  }
}

@keyframes slideInDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-5px);
  }
  75% {
    transform: translateX(5px);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .welcome-page {
    padding: 16px;
  }
  
  .welcome-logo h1 {
    font-size: 2.5rem;
  }
  
  .welcome-subtitle {
    font-size: 1rem;
  }
  
  .auth-card {
    padding: 32px 24px;
  }
  
  .welcome-features {
    grid-template-columns: 1fr;
    gap: 24px;
  }
  
  .feature {
    padding: 24px 20px;
  }
}

@media (max-width: 480px) {
  .welcome-logo h1 {
    font-size: 2rem;
  }
  
  .logo-icon {
    width: 60px;
    height: 60px;
    font-size: 1.5rem;
  }
  
  .auth-card {
    padding: 24px 20px;
  }
  
  .auth-tabs {
    margin-bottom: 24px;
  }
  
  .auth-tab {
    padding: 10px 16px;
    font-size: 0.9rem;
  }
}